
/**
 * 
 *  all request 
 * 
 */

import axios from 'axios'
const url = 'https://cmsdev.app-link.org/alucard263096/blog/api/'
          // http://cmsdev.app-link.org/alucard263096/blog/api/

          // goods_thumb --> http://applinkupload.oss-cn-shenzhen.aliyuncs.com/alucard263096/blog/goods/ ......

export default class requests {
  static async getNewslist() { //
    try {
      const res = await axios.get(url + 'news/newslist')
      if(res.status === 200 && res.statusText === 'OK') {
        // console.log('getNewslist data:', res.data)
        return res.data
      }
      return null
    } catch (e) {
      console.log(e)
      return null
    }
  }
  static async getNews(id) { //
    try {
      const res = await axios.get(url + 'news/newsdetail?id=' + id)
      if(res.status === 200 && res.statusText === 'OK') {
        return res.data
      }
      return null
    } catch (e) {
      console.log(e)
      return null
    }
  }
  static async getGoodslist() { //
    try {
      const res = await axios.get(url + 'goods/goodslist')
      if(res.status === 200 && res.statusText === 'OK') {
        return res.data
      }
      return null
    } catch (e) {
      console.log(e)
      return null
    }
  }
  static async getGoodscate() { //
    try {
      const res = await axios.get(url + 'category/catelist')
      // console.log('getGoodscate data:', res)
      if(res.status === 200 && res.statusText === 'OK') {
        let data = res.data
        let filter = ['']
        for(let v of data) {//
          filter.push(v.name)
        }
        // console.log('filter:', filter)
        return filter
      }
      return null
    } catch (e) {
      console.log(e)
      return null
    }
  }
  static async getGoods(id) { //
    try {
      const res = await axios.get(url + 'goods/goodsdetail?id=' + id)
      if(res.status === 200 && res.statusText === 'OK') {
        // 
        // console.log('getGoods data:', res)
        res.data.content = this.HtmlDecode(res.data.content)
        return res.data
      }
      return null
    } catch (e) {
      console.log(e)
      return null
    }
  }
  static async doPurchase(id, num) { // idid num
    try {
      const res = await axios.get(url + 'goods/goodsbuy?id=' + id + '&num=' + num)
      if(res.status === 200 && res.statusText === 'OK') {
        return res.data
      }
      return null
    } catch (e) {
      console.log(e)
      return null
    }
  }
  static async getFuncList() { //
    try {
      const res = await axios.get(url + 'func/funclist')
      if(res.status === 200 && res.statusText === 'OK') {
        return res.data
      }
      return null
    } catch (e) {
      console.log(e)
      return null
    }
  }
  static HtmlDecode(str) { // 
    var s = "";
    if (str.length == 0) return "";
    s = str.replace(/&amp;/g, "&");
    s = s.replace(/&lt;/g, "<");
    s = s.replace(/&gt;/g, ">");
    s = s.replace(/&nbsp;/g, " ");
    s = s.replace(/&#39;/g, "\'");
    s = s.replace(/&quot;/g, "\""); 
  
  
    s = s.replace(new RegExp("</p >", "gm"), "</p ><br />");
    s = s.replace(new RegExp("\"/alucard263096/ariesmall/upload/", "gm"), "\"" + "https://cmsdev.app-link.org/alucard263096/ariesmall/upload/");
  
  
    return s;
  }
  static async getCollectionList() { //
    try {
      const res = await axios.get(url + 'collection/getlist')
      if(res.status === 200 && res.statusText === 'OK') {
        return res.data
      }
      return null
    } catch (e) {
      console.log(e)
      return null
    }
  }
  static async addCollection(name, link, label) { // name: link: label:
    try {
      const res = await axios.get(url + 'collection/insert?name=' + name + '&link=' + link + '&label=' + label)
      if(res.status === 200 && res.statusText === 'OK') {
        // return res.data
        //  
        return this.getCollectionList()
      }
      return null
    } catch (e) {
      console.log(e)
      return null
    }
  }
  static async delCollection(id) { //
    try {
      const res = await axios.get(url + 'collection/delete?id=' + id)
      if(res.status === 200 && res.statusText === 'OK') {
        // return res.data
        //  
        return this.getCollectionList()
      }
      return null
    } catch (e) {
      console.log(e)
      return null
    }
  }
}



