<template>
  <div class="main">
    
    <div class="headimg">
      <img src="static/img/banner1.jpg" alt="">
    </div>
    <div class="content">
      <div class="createForm">
        <p class="title"></p>
        <div class="createBox">
          <div class="formLabel">
            <input type="text" v-model="nameInput" placeholder="">
          </div>
          <div class="formLabel">
            <input type="text" v-model="linkInput" placeholder="">
          </div>
          <div class="formLabel">
            <input type="text" v-model="labelInput" placeholder="">
          </div>
        </div>
        <div class="formBtn" @click="addCollection"></div>
      </div>
    </div>
    <goback path="/collection"></goback>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'createColl',
  data(){
    return {
      msg: 'this is createColl',
      nameInput: '',
      linkInput: '',
      labelInput: '',
    }
  },
  created() {
  },
  methods: {
    ...mapActions([
      'addCollList',
    ]),
    async addCollection() {
      let name = this.nameInput.trim()
      let link = this.linkInput.trim()
      let label = this.labelInput.trim()
      if(name != "" && link != "" && label != "") {
        await this.addCollList({name: name, link: link, label: label})
        this.nameInput = this.linkInput = this.labelInput = ""
        // alert('')
        this.$toast({msg: '', type: 'success', duration: 2000, position: 'top'})
        this.$router.push({path: '/collection'})
      }else {
        // alert('!')
        this.$toast({msg: '', type: 'warn', duration: 2000, position: 'top'})
      }
    },
  },
  computed: {
  }
}
</script>

<style scoped>
@import './collection.css';
</style>



